import { Radio, Slider, Switch } from 'antd';
import 'antd/dist/antd.css';
import React, { Component } from "react";
import PAESI from "../constants/paesi";
import { Row, Col } from 'antd';
import { DATA_AGGIONAMENTO } from '../assets/dati';


class Opzioni extends Component {

    componentDidMount() {
    }


    ////////////////////////////////////////////////////////////////


    handleCheck = nome => (checked, event) => {
        //   console.log(nome)
        //   console.log(checked)
        //  console.log(event.target)
        ///this.setState({ [nome]: checked });

        this.props.onPaeseChange(nome, checked);

    };


    handleChange = (event) => {
        //this.setState({ [event.target.name]: event.target.value });
        // console.log(event)
        this.props.onTipoChange(event.target.value);
    };



    handleChangeUltimoMese = name => event => {
        this.setState({ ultimoMese: event.target.checked });
    };


    renderCheck = (nome, etichetta) => {

        return (

            <Switch checkedChildren={etichetta} key={nome}
                unCheckedChildren={etichetta}
                onChange={this.handleCheck(nome)} checked={this.props[nome]}
            />
        );
    };

    ////////////////////////////////////

    handleSlider = (range) => {
   //     console.log(range)
        this.props.onRangeChange(range);
    }

    ///////////////////////////////////


    formatTootip = (valore) => {

        let numGiorni = -parseInt(valore);

        if (numGiorni === 0) return DATA_AGGIONAMENTO;
        if (numGiorni === 1) return "1 giorno prima";
        return numGiorni + " giorni prima"

    }




    render() {



        return (
            <div>
                
                <Row>
                    <Col>
                    <fieldset>
                        <legend> Opzioni </legend>  

                
                         <div className="switchPaesi">
                            Paesi: {PAESI.map(paese => this.renderCheck(paese.codice, paese.descrizione))}
                        </div>

                        <div className="tipoStats">
                            <Radio.Group name="tipo" onChange={this.handleChange} value={this.props.tipo}
                                buttonStyle="solid">
                                <Radio.Button value={"NewDeaths"}>Nuovi decessi</Radio.Button>
                                <Radio.Button value={"NewConfCases"}>Nuovi contagi</Radio.Button>
                            </Radio.Group>
                        </div>

                    <div  className="slider">
                        Periodo di rilevazione:

                  
                        <Slider min={-81} max={0} step={1} range value={this.props.range}
                            tooltipVisible={true}
                            tipFormatter={this.formatTootip}
                            
                            onChange={this.handleSlider} />
                   </div>

</fieldset>




                   </Col>

                   </Row>

               

            </div>


        );
    }
}

export default Opzioni;

